<template>
	<div>
		<AppHeader></AppHeader>
		<div class="detail">
			<div class="detail__list">
			  <div id="container"></div>
			  <hr />
			  <wrc-cell :arr="$store.state.column.dataArr" collectionName="column"></wrc-cell>
			</div>
		</div>
		<AppFooter></AppFooter>
	</div>
</template>

<script>
	import AppHeader from "../components/header/header.vue";
	import AppFooter from "../components/footer/footer.vue";
	import WrcCell from "../components/wrc-cell/wrc-cell.vue";
	import {mapActions} from "vuex";
	
	export default{
		name:"follow",
		components:{			
			AppHeader,
			AppFooter,
			WrcCell
		},
		
		// methods:{
		// 	...mapActions('column',['column'])
		// },
		mounted() {
			// this.column({name:"column"});
			this.$store.dispatch("column/column",{name:"column"})
			
			//地图
			var map = new BMapGL.Map('container'); // 创建Map实例
			map.centerAndZoom(new BMapGL.Point(121.651947, 31.260279), 14); // 初始化地图,设置中心点坐标和地图级别
			map.enableScrollWheelZoom(true); // 开启鼠标滚轮缩放
			
			//创建点标记
			var marker = new BMapGL.Marker(new BMapGL.Point(121.651947, 31.260279));
			//在地图上添加点标记
			map.addOverlay(marker);
		}
	}
</script>

<style scoped="scoped">
	.detail__list{
		padding: 0.8rem 0;
	}
	#container{
		/* width: 100%; */
		height: 10rem;
	}
</style>